//   }

//   componentDidUpdate(){

//   }
//   componentWillUnmount(){

//   }
// }

let YellowBtn = styled.button`
  background: ${(props) => props.bg};
  color: ${(props) => (props.bg == "blue" ? "white" : "black")};
  padding: 10px;
`;
let Box = styled.div`
  background: black;
  color: white;
  padding: 10px;
`;

function Detail(props) {

  let {재고, shoes} = useContext(Context1); //보관함 해체
  let [fade, setFade] = useState('');
  let [tab, setTab] = useState(0); 
  let { id } = useParams(); //디스트럭쳐링 문법
  let dispatch = useDispatch();
  let 찾은상품 = props.shoes.find(function (x) {
    return x.id == id;
  });
  let copyShoes = props.shoes;

  useEffect(()=>{
    //localStorage.setItem('watched',JSON.stringify([]))
    let b= setTimeout(()=>{setFade('end')},100);
    let storageData = localStorage.getItem('watched') //문자열 
    console.log(storageData);
    storageData = JSON.parse(storageData) //오브젝트 ({} [] )
    storageData.push((찾은상품.id).toString()) //number
    console.log(storageData)
    storageData = new Set(storageData)
    storageData = Array.from(storageData)
    localStorage.setItem('watched',JSON.stringify(storageData))
    return()=>{
      clearTimeout(b);
      setFade('');
    }
  },[] )

  return (
    <div className={'container '+'start '+fade}>
      <div className='row'>
        <div className='col-md-6'>
          <img
            src={`https://codingapple1.github.io/shop/shoes${찾은상품.id}.jpg`}
            width='100%'
          />
          {console.log(찾은상품.id)}
        </div>
        <div className='col-md-6 mt-4'>
          <h4 className='pt-5'>{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}원</p>
          <button className='btn btn-danger' onClick={()=>{
            dispatch(addItem({id : 1, name : 'red knit', count : 1} ))
            }}>주문하기</button>
       
        </div>
      </div>

      <Nav variant='tabs' defaultActiveKey='link0'> 
      {/* 디폴트 키: 화면  마운트시 처음 켤거 설정 */}
        <Nav.Item>
          <Nav.Link eventKey='link0' onClick={()=>{setTab(0)}}>{재고[0]}버튼0</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey='link1' onClick={()=>{setTab(1)}}>버튼1</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey='link2' onClick={()=>{setTab(2)}}>버튼2</Nav.Link>
        </Nav.Item>
      </Nav>
      {/* 탭이 1이면 첫번째디브
      탭이 2면 두번째디브
      탭이 3이면 세번재디브 
      보여주세요 */}
      <TabContent tab = {tab} shoes={props.shoes}/>
    </div>
  );
}

// html 밖에다 조건문 생성 (3항쓰기 싫어서)
// 컴포넌트는 리턴문이 없으면 동작안함.
function TabContent({tab,shoes}){
 //탭 변할때마다 엔드 변하게 해주세요
  let [fade, setFade] = useState('');
  let {재고} = useContext(Context1);
  useEffect(()=>{
    let a = setTimeout(()=>{setFade('end');},100);
    
    return()=>{
      //클린업 펑션 등장
      setFade('');
      clearTimeout();

    }
  },[tab]) 

  return <div className={'start '+ fade}>
    {[<div>{shoes[0].title}내용0</div>,<div>{재고}내용1</div>,<div>내용2</div>][tab]}
  </div>

  // if(props.tab ==0){
  //   return <div>내용0</div>
  // }
  // else if(props.tab==1){
  //   return <div>내용1</div>
  // }
  // else if(props.tab==2){
  //   return <div>내용2</div>
  // }
}

export default Detail;
